import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {

    arrContact = [];
    id: number = 1;
    fname: string="";
    lname: string="";
    email: string="";
    phone: string="";
    status: string="";
    add: boolean = true;
    update: boolean = false;

    constructor() { }

    ngOnInit() {
        
  }

    

    addContact()
    {
        debugger;
        if (this.fname != "" &&
            this.lname != "" &&
            this.email != "" &&
            this.phone != ""
           ) {
            this.arrContact.push({
                id: this.id,
                fname: this.fname,
                lname: this.lname,
                email: this.email,
                phone: this.phone,
                status: this.status
            })

            this.id = this.id + 1;
            console.log(this.arrContact);

            this.fname = "";
            this.lname = "";
            this.email = "";
            this.phone = "";
            this.status ="";
        }
        else {
            alert('Please insert all mandatory fields.');
        }
        
    }

    editContact(index)
    {
        this.id = this.arrContact[index].id;
        this.fname = this.arrContact[index].fname;
        this.lname = this.arrContact[index].lname;
        this.email = this.arrContact[index].email;
        this.phone = this.arrContact[index].phone;
        this.status = this.arrContact[index].status;

        this.add = false;
        this.update = true;

    }

    updateContact()
    {  
        if (this.fname != "" &&
            this.lname != "" &&
            this.email != "" &&
            this.phone != ""
        ) {
            this.add = true;
            this.update = false;

            for (var i in this.arrContact) {
                if (this.arrContact[i].id == this.id) {
                    this.arrContact[i].id = this.id;
                    this.arrContact[i].fname = this.fname;
                    this.arrContact[i].lname = this.lname;
                    this.arrContact[i].email = this.email;
                    this.arrContact[i].phone = this.phone;
                    this.arrContact[i].status = this.status;
                }
            }

        this.fname = "";
        this.lname = "";
        this.email = "";
        this.phone = "";
        this.status = "";
        }
        else {
            alert('Please insert all mandatory fields.');
        }
    }

    deleteContact(index)
    {

        var result = confirm("Want to delete?");
        if (result == true) {
            this.arrContact.splice(index, 1);
        }

        this.fname = "";
        this.lname = "";
        this.email = "";
        this.phone = "";
        this.status = "";
    }
}
